interface ISelectListProps {
    options: JSX.Element[];
    handleChange: (target: HTMLSelectElement) => void;
    id: string;
    value: string;
    classNames?: string;
    labelValue: string;
}

const SelectList = (props: ISelectListProps) => {
    return (
        <>
            <label htmlFor="todoCategoryId" className="text-white font-semibold">{props.labelValue}</label>
            <select id={props.id}
                value={props.value}
                onChange={(e) => props.handleChange(e.target)}
                className={"p-1 " + props.classNames}
            >
                <option key="" value="-1"></option>
                {props.options}
            </select>
        </>
    )
}

export default SelectList;