import { useContext, useState } from "react"
import { Redirect } from "react-router";
import Button from "../../components/Button";
import Input from "../../components/Input";
import Loader from "../../components/Loader";
import { AppContext } from "../../context/AppContext";
import { ILoginValues } from "../../dtos/ILoginValues";
import { AccountService } from "../../services/account-service";
import { EPageStatus } from "../../types/EPageStatus";
import { IJwtResponse } from "../../types/IJwtResponse";
import { IPageStatus } from "../../types/IPageStatus";

const initialFormValues: ILoginValues = {
    email: "",
    password: "",
};

export interface ILoginProps {
    values: ILoginValues;
    handleChange: (target: HTMLInputElement) => void;
    toggleAuthStatus: (target: EventTarget) => void;
    pageStatus: IPageStatus;
}


const LoginForm = () => {
    const [formValues, setFormValues] = useState(initialFormValues);

    const [pageStatus, setPageStatus] = useState({
        pageStatus: EPageStatus.Ok, statusCode: 0
    } as IPageStatus);

    const appState = useContext(AppContext);

    const toggleAuthStatus = async (e: EventTarget) => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 })
        let result = await AccountService.login({
            email: formValues.email,
            password: formValues.password
        });

        if (result.ok && result.data) {
            const jwtResponse = result.data as unknown as IJwtResponse;
            appState.setAuthInfo(jwtResponse.token, jwtResponse.firstName, jwtResponse.lastName);
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: 0 })

        }
        setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages })

    }

    const handleChange = (target: HTMLInputElement) => {
        if (target.id === "email") {
            setFormValues({ ...formValues, email: target.value })
        }
        if (target.id === "password") {
            setFormValues({ ...formValues, password: target.value })
        }

    }


    return (
        <>
            {appState.jwt === null
                ?
                <FormView
                    values={formValues}
                    handleChange={handleChange}
                    toggleAuthStatus={toggleAuthStatus}
                    pageStatus={pageStatus} />
                :
                <Redirect to="/todos" />
            }
        </>

    )
}


const FormView = (props: ILoginProps) => {
    return (
        <>
            <div className="flex justify-center p-2 border-t border-b border-1">
                <form className="p-2 w-96 sm:w-120 text-white space-y-4">
                    <Input
                        type="email"
                        value={props.values.email ?? ""}
                        handleChange={props.handleChange}
                        labelValue="Email"
                        id="email"
                        autoComplete="email"
                    />
                    <Input
                        type="password"
                        value={props.values.password ?? ""}
                        handleChange={props.handleChange}
                        labelValue="Password"
                        id="password"
                        autoComplete="password"
                    />
                    <Button
                        classNames="bg-green-500 w-full text-white"
                        action={props.toggleAuthStatus}
                        value="Login"
                    />
                </form>
            </div>
            <Loader pageStatus={props.pageStatus} />
        </>
    )
}

export default LoginForm;
