import { ITodoCategory } from "../../dtos/ITodoCategory";
import { ITodoPriority } from "../../dtos/ITodoPriority";
import { ITodoTask } from "../../dtos/ITodoTask";
import { IPageStatus } from "../../types/IPageStatus";
import DatePicker from "react-datepicker";
import { Link } from "react-router-dom";
import Input from "../../components/Input";
import React from "react";
import SelectList from "../../components/Select";
import Button from "../../components/Button";
import SelectOption from "../../components/SelectOption";


export interface ITodoAddProps {
    values: ITodoTask;
    handleChange: (target: HTMLInputElement | HTMLSelectElement) => void;
    handleDateChange: (target: Date) => void;
    addTodo: (target: EventTarget) => void;
    pageStatus: IPageStatus;
    categories: ITodoCategory[];
    priorities: ITodoPriority[];
    date: Date;
    generateDefaultPriorities: () => void;
    generateDefaultCategories: () => void;
}

const TodosAdd = (props: ITodoAddProps) => {

    return (
        <div className="flex justify-center p-2 border-t border-b border-1">
            <form className="p-2 w-96 sm:w-120 text-black">
                <div className="flex w-full flex-col space-y-4">
                    <div className="flex flex-col space-y-4">
                        <Input
                            type="text"
                            classNames="p-1 bg-white"
                            value={props.values.taskName}
                            handleChange={props.handleChange}
                            labelValue="What do you need to do?"
                            id="taskName"
                        />
                        <div className="flex flex-col">
                            <label className="text-white font-semibold">When does it have to be done?</label>
                            <DatePicker
                                className="w-full"
                                selected={new Date(props.date)}
                                onChange={(date) => props.handleDateChange(date as Date)}
                                dateFormat="dd.MM.yyyy"
                                minDate={new Date(Date.now())}
                            />
                        </div>
                    </div>
                    <div className="flex flex-row justify-between">
                        <div className="flex flex-col w-1/3">
                            {
                                props.categories.length > 0
                                    ?
                                    <>
                                        <SelectList
                                            options={props.categories.map(category => {
                                                return (
                                                    <SelectOption
                                                        value={category.id ?? ""}
                                                        key={category.id ?? ""}
                                                        displayName={category.categoryName}
                                                    />
                                                )
                                            })}
                                            id="todoCategoryId"
                                            value={props.values.todoCategoryId}
                                            handleChange={props.handleChange}
                                            labelValue="Categorize!"
                                        />
                                    </>
                                    :
                                    <>
                                        <Link
                                            className="p-2 bg-green-600 hover:text-pink-100 font-semibold text-white text-center"
                                            to="/categories">
                                            Add categories
                                        </Link>
                                        <span className="text-white">or</span>
                                        <Button
                                            action={props.generateDefaultCategories}
                                            classNames="hover:text-pink-100 text-white text-left bg-green-600 w-full"
                                            value="Generate default"
                                        />
                                    </>
                            }
                        </div>
                        <div className="flex flex-col w-1/3">
                            {
                                props.priorities.length > 0
                                    ?
                                    <SelectList
                                        options={props.priorities.map(priority => {
                                            return (
                                                <SelectOption
                                                    value={priority.id ?? ""}
                                                    key={priority.id ?? ""}
                                                    displayName={priority.priorityName}
                                                />
                                            )
                                        })}
                                        id="todoPriorityId"
                                        value={props.values.todoPriorityId}
                                        handleChange={props.handleChange}
                                        labelValue="Prioritize!"
                                    />
                                    :
                                    <>
                                        <Link
                                            className="p-2 bg-green-600 hover:text-pink-100 font-semibold text-white text-center"
                                            to="/priorities">
                                            Add priorities
                                        </Link>
                                        <span className="text-white">or</span>
                                        <Button
                                            action={props.generateDefaultPriorities}
                                            classNames="hover:text-pink-100 text-white text-left bg-green-600 w-full"
                                            value="Generate default"
                                        />
                                    </>

                            }
                        </div>
                    </div>
                    <Button
                        action={props.addTodo}
                        classNames="bg-green-600 w-full text-white"
                        value="Add"
                    />
                </div>
            </form>
        </div>
    )
}

export default TodosAdd;