import React from "react";
import { ITodoCategory } from "../../dtos/ITodoCategory";
import { ITodoPriority } from "../../dtos/ITodoPriority";
import { ITodoTask } from "../../dtos/ITodoTask";
import DatePicker from "react-datepicker";
import Input from "../../components/Input";
import SelectList from "../../components/Select";
import Button from "../../components/Button";
import SelectOption from "../../components/SelectOption";

export interface ITodosEditProps {
    values: ITodoTask;
    editTodo: (target: EventTarget) => void;
    handleChange: (target: HTMLInputElement | HTMLSelectElement) => void;
    handleDateChange: (target: Date) => void;
    categories: ITodoCategory[];
    priorities: ITodoPriority[];
    archiveTodo: (target: EventTarget) => void;
    deleteTodo: (target: EventTarget) => void;
}

const TodosEdit = (props: ITodosEditProps) => {
    return (
        <div className="flex justify-center p-2 border-t border-b border-1">
            <form className="p-2 w-96 sm:w-120 text-black">
                <div className="flex w-full flex-col space-y-4">
                    <div className="flex flex-col space-y-4">
                        <Input
                            type="text"
                            classNames="p-1 bg-white"
                            value={props.values.taskName}
                            handleChange={props.handleChange}
                            labelValue="What do you need to do?"
                            id="taskNameEdit"
                        />
                        <div className="flex flex-col">
                            <label className="text-white font-semibold">When does it have to be done?</label>
                            <DatePicker
                                className="w-full"
                                selected={new Date(props.values.dueDt)}
                                onChange={(date) => props.handleDateChange(date as Date)}
                                dateFormat="dd.MM.yyyy"
                                minDate={new Date(Date.now())}
                            />
                        </div>
                    </div>
                    <div className="flex flex-row justify-between">
                        <div className="flex flex-col w-1/3">
                            <SelectList
                                options={props.categories.map(category => {
                                    return (
                                        <SelectOption
                                            value={category.id ?? ""}
                                            key={category.id ?? ""}
                                            displayName={category.categoryName}
                                        />
                                    )
                                })}
                                id="todoCategoryIdEdit"
                                value={props.values.todoCategoryId}
                                handleChange={props.handleChange}
                                labelValue="Categorize!"
                            />
                        </div>
                        <div className="flex flex-col w-1/3">
                            <SelectList
                                options={props.priorities.map(priority => {
                                    return (
                                        <SelectOption
                                            value={priority.id ?? ""}
                                            key={priority.id ?? ""}
                                            displayName={priority.priorityName}
                                        />
                                    )
                                })}
                                id="todoPriorityIdEdit"
                                value={props.values.todoPriorityId}
                                handleChange={props.handleChange}
                                labelValue="Prioritize!"
                            />
                        </div>
                    </div>
                    <div className="flex flex-row">
                        <Button
                            action={props.editTodo}
                            classNames="bg-yellow-200 text-black w-full"
                            value="Edit"
                        />
                        {
                            props.values.isArchived
                                ?
                                <Button
                                    action={props.deleteTodo}
                                    classNames="bg-red-300 text-black w-full"
                                    value="Delete permanently"
                                />
                                :
                                <Button
                                    action={props.archiveTodo}
                                    classNames="bg-red-200 text-black w-full"
                                    value="Archive"
                                />
                        }
                    </div>
                </div>
            </form>
        </div>
    )
}

export default TodosEdit;