import { useContext, useEffect, useState } from "react";
import { Redirect } from "react-router";
import Button from "../../components/Button";
import Filter from "../../components/Filter";
import Input from "../../components/Input";
import Loader from "../../components/Loader";
import SelectOption from "../../components/SelectOption";
import { AppContext } from "../../context/AppContext";
import { ITodoCategory } from "../../dtos/ITodoCategory";
import { ITodoPriority } from "../../dtos/ITodoPriority";
import { ITodoTask } from "../../dtos/ITodoTask";
import { BaseService } from "../../services/base-service";
import { EPageStatus } from "../../types/EPageStatus";
import { IPageStatus } from "../../types/IPageStatus";
import TodoList from "./TodoList";
import TodosAdd from "./TodosAdd";
import TodosEdit from "./TodosEdit";

const initialFormValues: ITodoTask = {
    taskName: "",
    taskSort: 0,
    dueDt: new Date(Date.now()),
    isCompleted: false,
    isArchived: false,
    todoCategoryId: "",
    todoPriorityId: "",
}

export const initialSelectedTodo: ITodoTask = {
    id: "",
    taskName: "",
    taskSort: 0,
    dueDt: new Date(Date.now()),
    isCompleted: false,
    isArchived: false,
    todoCategoryId: "",
    todoPriorityId: "",
}

interface ITodoFilters {
    categoryFilters: string;
    priorityFilters: string;
    hideDone: string;
    searchValue: string;
}

const initialTodoFilters: ITodoFilters = {
    categoryFilters: "",
    priorityFilters: "",
    hideDone: "",
    searchValue: "",

}
let filters = {} as ITodoFilters;

const TodosIndex = () => {
    const [formValues, setFormValues] = useState(initialFormValues);
    const [todos, setTodos] = useState([] as ITodoTask[]);
    const [pageStatus, setPageStatus] = useState({ pageStatus: EPageStatus.Loading, statusCode: -1 } as IPageStatus);
    const [categories, setCategories] = useState([] as ITodoCategory[]);
    const [priorities, setPriorities] = useState([] as ITodoPriority[]);
    const [dueDate, setDueDate] = useState(new Date(Date.now()));
    const [selectedTodo, setSelectedTodo] = useState(initialSelectedTodo)
    const [archivedTodos, setArchivedTodos] = useState([] as ITodoTask[]);
    const [currentFilters, setCurrentFilters] = useState(initialTodoFilters);
    const [filteredTodos, setfilteredTodos] = useState([] as ITodoTask[]);

    const appState = useContext(AppContext);

    const loadData = async () => {
        let result = await BaseService.getAll<ITodoTask>('/TodoTasks', appState.jwt ?? "");

        if (result.ok && result.data) {
            const filterResult = result.data.filter(todo => todo.isArchived === false);
            const filterArchived = result.data.filter(todo => todo.isArchived === true);
            setfilteredTodos(filterResult);
            setTodos(filterResult);
            setArchivedTodos(filterArchived);

            let categoryResult = await BaseService.getAll<ITodoCategory>('/TodoCategories', appState.jwt ?? "");

            if (categoryResult.ok && categoryResult.data) {
                setCategories(categoryResult.data);
            }

            let priorityResult = await BaseService.getAll<ITodoPriority>('/TodoPriorities', appState.jwt ?? "");
            if (priorityResult.ok && priorityResult.data) {
                setPriorities(priorityResult.data);
            }
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });

        } else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages });
        }
    }

    const handleChange = (target: HTMLInputElement | HTMLSelectElement) => {
        switch (target.id) {
            case "taskName":
                setFormValues({ ...formValues, taskName: target.value });
                return;
            case "todoCategoryId":
                setFormValues({ ...formValues, todoCategoryId: target.value });
                return;
            case "todoPriorityId":
                setFormValues({ ...formValues, todoPriorityId: target.value });
                return;
            case "taskNameEdit":
                setSelectedTodo({ ...selectedTodo, taskName: target.value })
                return;
            case "todoCategoryIdEdit":
                setSelectedTodo({ ...selectedTodo, todoCategoryId: target.value })
                return;
            case "todoPriorityIdEdit":
                setSelectedTodo({ ...selectedTodo, todoPriorityId: target.value })
                return;
            case "categoryFilter":
                setCurrentFilters({ ...currentFilters, categoryFilters: target.value });
                filters = { ...filters, categoryFilters: target.value } as ITodoFilters;
                filterTodos(filters);
                return;
            case "priorityFilter":
                setCurrentFilters({ ...currentFilters, priorityFilters: target.value });
                filters = { ...filters, priorityFilters: target.value } as ITodoFilters;
                filterTodos(filters);
                return;
            case "hideDone":
                setCurrentFilters({ ...currentFilters, hideDone: target.value });
                filters = { ...filters, hideDone: target.value } as ITodoFilters;
                filterTodos(filters);
                return;
            case "searchValue":
                setCurrentFilters({ ...currentFilters, searchValue: target.value });
                filters = { ...filters, searchValue: target.value } as ITodoFilters;
                filterTodos(filters);
                return;
            default:
                return;
        }

    }

    const handleDateChange = (target: Date) => {
        setDueDate(target)

        setFormValues({
            ...formValues,
            dueDt: target
        })
    }

    const handleEditDateChange = (target: Date) => {
        setSelectedTodo({
            ...selectedTodo,
            dueDt: target
        })
    }

    const addTodo = async (e: EventTarget) => {
        setPageStatus({
            pageStatus: EPageStatus.Loading, statusCode: -1
        });

        let result = await BaseService.post<ITodoTask>('/TodoTasks', formValues, appState.jwt ?? "")


        if (result.ok) {
            setFormValues(initialFormValues);
            setDueDate(new Date(Date.now()))
            setPageStatus({
                pageStatus: EPageStatus.Ok, statusCode: result.statusCode,
            })
            loadData();
        } else {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages,
            })
        }
    }

    const editTodo = async (e: EventTarget) => {
        setPageStatus({
            pageStatus: EPageStatus.Loading, statusCode: -1
        })

        let result = await BaseService.put<ITodoTask>('/TodoTasks', selectedTodo, selectedTodo.id ?? "", appState.jwt ?? "")
        if (result.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Ok, statusCode: result.statusCode
            })
            setSelectedTodo(initialSelectedTodo);
            loadData();
        } else {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages
            })
        }
    }

    const setTodoCompletedValue = async (todo: ITodoTask) => {
        todo.isCompleted ? todo.isCompleted = false : todo.isCompleted = true;
        if (!todo.isCompleted) {
            todo.isArchived = false;
        }

        setPageStatus({
            pageStatus: EPageStatus.Loading, statusCode: -1
        })

        let result = await BaseService.put<ITodoTask>('/TodoTasks', todo, todo.id ?? "", appState.jwt ?? "")

        if (result.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Ok, statusCode: result.statusCode
            })
            loadData();
        } else {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages
            })
        }

    }

    const generateDefaultPriorities = async () => {

        const lowPriority = { priorityName: "Low", prioritySort: 0 } as ITodoPriority;
        const normalPriority = { priorityName: "Normal", prioritySort: 1 } as ITodoPriority;
        const highPriority = { priorityName: "High", prioritySort: 2 } as ITodoPriority;

        setPageStatus({
            pageStatus: EPageStatus.Loading, statusCode: -1
        })

        let lowResult = await BaseService.post<ITodoPriority>('/TodoPriorities', lowPriority, appState.jwt ?? "");
        if (!lowResult.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: lowResult.statusCode, messages: lowResult.messages
            })
            return;
        }

        let normResult = await BaseService.post<ITodoPriority>('/TodoPriorities', normalPriority, appState.jwt ?? "");
        if (!normResult.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: normResult.statusCode, messages: normResult.messages
            })
            return;
        }

        let highResult = await BaseService.post<ITodoPriority>('/TodoPriorities', highPriority, appState.jwt ?? "");
        if (!highResult.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: highResult.statusCode, messages: highResult.messages
            })
            return;
        }

        setPageStatus({
            pageStatus: EPageStatus.Ok, statusCode: highResult.statusCode
        })

        loadData();
    }

    const generateDefaultCategories = async () => {

        const homeCategory = { categoryName: "Home", categorySort: 0 } as ITodoCategory;
        const workCategory = { categoryName: "Work", categorySort: 1 } as ITodoCategory;

        setPageStatus({
            pageStatus: EPageStatus.Loading, statusCode: -1
        })

        let homeResult = await BaseService.post<ITodoCategory>('/TodoCategories', homeCategory, appState.jwt ?? "");
        if (!homeResult.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: homeResult.statusCode, messages: homeResult.messages
            })
            return;
        }

        let workResult = await BaseService.post<ITodoCategory>('/TodoCategories', workCategory, appState.jwt ?? "");
        if (!workResult.ok) {
            setPageStatus({
                pageStatus: EPageStatus.Error, statusCode: workResult.statusCode, messages: workResult.messages
            })
            return;
        }

        setPageStatus({
            pageStatus: EPageStatus.Ok, statusCode: workResult.statusCode
        })

        loadData();
    }

    const archiveTodo = async () => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });

        selectedTodo.isArchived = true;

        const result = await BaseService.put<ITodoTask>("/TodoTasks", selectedTodo, selectedTodo.id ?? "", appState.jwt ?? "");

        if (result.ok) {
            let archivedTodo = todos.splice(todos.findIndex(todo => todo.id === selectedTodo.id), 1);
            setTodos(todos);
            archivedTodos.push(archivedTodo[0])
            setSelectedTodo(initialSelectedTodo);
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
        }
        else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages });
        }
    }

    const deleteTodo = async () => {
        setPageStatus({ pageStatus: EPageStatus.Loading, statusCode: -1 });

        const result = await BaseService.delete<ITodoTask>("/TodoTasks", selectedTodo.id ?? "", appState.jwt ?? "");

        if (result.ok) {
            archivedTodos.splice(todos.findIndex(todo => todo.id === selectedTodo.id), 1);
            setArchivedTodos(archivedTodos);
            setPageStatus({ pageStatus: EPageStatus.Ok, statusCode: result.statusCode });
        }
        else {
            setPageStatus({ pageStatus: EPageStatus.Error, statusCode: result.statusCode, messages: result.messages });

        } setSelectedTodo(initialSelectedTodo);
        setFormValues(initialFormValues);
    }

    const filterTodos = (filters: ITodoFilters) => {
        let filteredValues = todos;
        if (filters.categoryFilters !== "-1" && filters.categoryFilters !== undefined) {
            console.log("categoryFilter");
            filteredValues = filteredValues.filter(todo => todo.todoCategoryId === filters.categoryFilters);

        }

        if (filters.priorityFilters !== "-1" && filters.priorityFilters !== undefined) {
            console.log("priorityFilter");
            filteredValues = filteredValues.filter(todo => todo.todoPriorityId === filters.priorityFilters);
        }

        if (filters.hideDone !== "-1" && filters.hideDone !== undefined) {
            console.log("hideDone");
            console.log(filters.hideDone);

            filteredValues = filteredValues.filter(todo => todo.isCompleted === (filters.hideDone === "yes" ? false : true));
        }

        if (filters.searchValue !== undefined && filters.searchValue.length > 1) {
            filteredValues = filteredValues.filter(todo => todo.taskName.toLowerCase().includes(filters.searchValue.toLowerCase()));
        }

        setfilteredTodos(filteredValues);
    }

    const panelIsVisible = (panelQuerySelector: string): boolean => {
        const panel = document.querySelector(panelQuerySelector);
        if (panel?.classList.contains("hidden")) {
            return false;
        }
        return true;
    }

    const setPanelVisible = (visible: boolean, openerQuerySelector: string, openableQuerySelector: string, value: string) => {
        const panel = document.querySelector(openerQuerySelector) as HTMLDivElement;
        const accordion = document.querySelector(openableQuerySelector) as HTMLDivElement;

        if (visible) {
            panel.classList.add("hidden");
            accordion.classList.add("h-auto");
            accordion.innerHTML = value;
        } else {
            panel.classList.remove("hidden");
            accordion.innerHTML = value;
        }
    }

    useEffect(() => {
        loadData();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    return (
        <>
            <Loader pageStatus={pageStatus} />
            {appState.jwt !== null
                ?
                <div className="flex lg:flex-row flex-col justify-evenly">
                    <div className="lg:hidden accordion cursor-pointer text-right"
                        onClick={() => panelIsVisible(".panel") ? setPanelVisible(true, ".panel", ".accordion", "â¬‡") : setPanelVisible(false, ".panel", ".accordion", "â¬†")}
                    ><span className="italic text-sm text-white"> Click to add new todos â†’ </span>â¬‡
                    </div>
                    <div className="flex-col lg:space-y-12 panel lg:flex hidden">
                        {
                            selectedTodo.id !== ""
                                ?
                                <TodosEdit
                                    values={selectedTodo}
                                    handleChange={handleChange}
                                    handleDateChange={handleEditDateChange}
                                    categories={categories}
                                    priorities={priorities}
                                    editTodo={editTodo}
                                    archiveTodo={archiveTodo}
                                    deleteTodo={deleteTodo}
                                />
                                :
                                <TodosAdd
                                    values={formValues}
                                    handleChange={handleChange}
                                    addTodo={addTodo}
                                    pageStatus={pageStatus}
                                    categories={categories}
                                    priorities={priorities}
                                    handleDateChange={handleDateChange}
                                    date={dueDate}
                                    generateDefaultPriorities={generateDefaultPriorities}
                                    generateDefaultCategories={generateDefaultCategories}
                                />
                        }
                    </div>
                    <div className="flex flex-col">
                        <div className="flex flex-row justify-center">
                            <div className="flex flex-row sm:w-120 w-96">
                                <Input
                                    handleChange={handleChange}
                                    id="searchValue"
                                    labelValue="Search"
                                    type="text"
                                    value={currentFilters.searchValue}
                                    classNames="w-full"
                                />

                                <div
                                    className="filter-accordion p-2 text-center flex cursor-pointer border-white rounded-md border m-auto text-white hover:text-blue-500 hover:bg-white"
                                    onClick={() => panelIsVisible(".filter-panel") ? setPanelVisible(true, ".filter-panel", ".filter-accordion", "Show filters") : setPanelVisible(false, ".filter-panel", ".filter-accordion", "Hide filters")}
                                >
                                    Show filters
                                </div>
                            </div>
                        </div>
                        <div className="flex flex-row justify-center filter-panel hidden">
                            <div className="flex flex-row sm:w-120 w-96 justify-between">
                                <Filter
                                    handleChange={handleChange}
                                    id="categoryFilter"
                                    labelValue="Category filter"
                                    value={currentFilters.categoryFilters}
                                    type="selectList"
                                    options={categories.map(category => {
                                        return (
                                            <SelectOption
                                                value={category.id ?? ""}
                                                key={category.id ?? ""}
                                                displayName={category.categoryName} />)
                                    })}
                                />
                                <Filter
                                    handleChange={handleChange}
                                    id="priorityFilter"
                                    labelValue="Priority filter"
                                    value={currentFilters.priorityFilters}
                                    type="selectList"
                                    options={priorities.map(priority => {
                                        return (
                                            <SelectOption
                                                value={priority.id ?? ""}
                                                key={priority.id ?? ""}
                                                displayName={priority.priorityName} />)
                                    })}
                                />
                                <Filter
                                    handleChange={handleChange}
                                    id="hideDone"
                                    labelValue="Hide done"
                                    type="selectList"
                                    value={currentFilters.hideDone}
                                    options={
                                        new Array(
                                            <>
                                                <SelectOption
                                                    value="yes"
                                                    key="1"
                                                    displayName="yes" />

                                            </>
                                        )
                                    }
                                />
                            </div>
                        </div>
                        <TodoList
                            values={filteredTodos}
                            setSelectedTodo={setSelectedTodo}
                            selectedTodo={selectedTodo}
                            setTodoCompletedValue={setTodoCompletedValue}
                        />
                        {
                            archivedTodos.length > 0
                                ?
                                <>
                                    <div className="archived-accordion cursor-pointer text-right"
                                        onClick={() => panelIsVisible(".archived-panel") ? setPanelVisible(true, ".archived-panel", ".archived-accordion", "ðŸ“") : setPanelVisible(false, ".archived-panel", ".archived-accordion", "ðŸ“‚")}>
                                        <span className="italic text-sm text-white">Show archived todos â†’ </span>ðŸ“
                                    </div>
                                    <div className="archived-panel hidden">
                                        <TodoList
                                            values={archivedTodos}
                                            setSelectedTodo={setSelectedTodo}
                                            selectedTodo={selectedTodo}
                                            setTodoCompletedValue={setTodoCompletedValue}
                                        />
                                    </div>
                                </>
                                :
                                <></>
                        }
                    </div>
                </div>
                :
                <Redirect to="/" />
            }

        </>
    )
}

export default TodosIndex;
