import Button from "../../components/Button";
import Input from "../../components/Input";
import { ITodoCategory } from "../../dtos/ITodoCategory";

interface ICategoryEditProps {
    handleEditChange: (target: HTMLInputElement) => void;
    values: ITodoCategory;
    editCategory: (target: EventTarget) => void;
}

const CategoryEdit = (props: ICategoryEditProps) => {
    return (
        <>
            <form>
                <div className="flex flex-col space-y-4 p-4 border-2 border-white rounded-md  text-white dark:bg-transparent">
                    <Input
                        type="text"
                        value={props.values.categoryName}
                        handleChange={props.handleEditChange}
                        labelValue="Category name"
                        id="categoryName"
                    />
                    <Input
                        type="number"
                        value={props.values.categorySort}
                        handleChange={props.handleEditChange}
                        labelValue="Priority"
                        id="categorySort"
                    />
                    <Button
                        classNames="text-black bg-yellow-300 w-full"
                        action={props.editCategory}
                        value="Edit"
                    />
                </div>
            </form>
        </>
    )
}

export default CategoryEdit;