interface IInputProps {
    type: string;
    classNames?: string;
    value: string | number;
    handleChange: (target: HTMLInputElement) => void;
    labelValue: string;
    id: string;
    autoComplete?: string;
}

const Input = (props: IInputProps) => {
    return (
        <div className="flex flex-col font-semibold">
            <label className="text-white" htmlFor={props.id}>
                {props.labelValue}
            </label>
            <input id={props.id}
                type={props.type}
                className={"p-1 bg-white text-black " + props.classNames}
                value={props.value}
                onChange={(e) => props.handleChange(e.target)}
            >
            </input>
        </div>
    )
}

export default Input;