import Button from "../../components/Button";
import Input from "../../components/Input";
import { ITodoCategory } from "../../dtos/ITodoCategory";

export interface CategoryAddProps {
    handleChange: (targer: HTMLInputElement) => void;
    values: ITodoCategory;
    addCategory: (target: EventTarget) => void;
}

const CategoryAdd = (props: CategoryAddProps) => {
    return (
        <>
            <form>
                <div className="flex flex-col space-y-4 p-4 border-2 border-white rounded-md  text-white dark:bg-transparent">
                    <Input
                        type="text"
                        classNames="p-1 bg-white text-black"
                        value={props.values.categoryName}
                        handleChange={props.handleChange}
                        labelValue="Category name"
                        id="categoryName"
                    />
                    <Input
                        type="number"
                        classNames="p-1 bg-white text-black"
                        value={props.values.categorySort}
                        handleChange={props.handleChange}
                        labelValue="Priority"
                        id="categorySort"
                    />
                    <Button
                        classNames="p-2 bg-green-600 w-full font-bold text-white"
                        action={props.addCategory}
                        value="Add"
                    />
                </div>
            </form>
        </>);
}

export default CategoryAdd;