import React from "react";
import Button from "../../components/Button";
import Input from "../../components/Input";
import { ITodoPriority } from "../../dtos/ITodoPriority";

export interface PriorityAddProps {
    handleChange: (targer: HTMLInputElement) => void;
    values: ITodoPriority;
    addPriority: (target: EventTarget) => void;
}

const PriorityAdd = (props: PriorityAddProps) => {
    return (
        <>
            <form>
                <div className="flex flex-col space-y-4 p-4 border-2 border-white rounded-md  text-white dark:bg-transparent">
                    <Input
                        type="text"
                        value={props.values.priorityName}
                        handleChange={props.handleChange}
                        labelValue="Priority Name"
                        id="priorityName"
                    />
                    <Input
                        type="text"
                        value={props.values.prioritySort}
                        handleChange={props.handleChange}
                        labelValue="Priority level"
                        id="prioritySort"
                    />
                    <Button
                        classNames="bg-green-600 w-full"
                        action={props.addPriority}
                        value="Add"
                    />
                </div>
            </form>
        </>);
}

export default PriorityAdd;