interface IButttonProps {
    classNames?: string;
    action: (target: EventTarget) => void;
    value: string;
    id?: string;
}

const Button = (props: IButttonProps) => {
    return (
        <div className="flex flex-row w-full">
            <button
                className={"p-2 font-bold text-center " + props.classNames}
                onClick={(e) => { props.action(e.target); e.preventDefault(); }}>
                {props.value}
            </button>
        </div>)
}

export default Button;